import { modelType } from "../interface/modelType";
import Target from "./Target";
import Vector3 from "./Vector3";

export default class Model {
    modelName: string;
    annotations: Target[];
    environment?: string;
    skybox: boolean;
    poster?: string;
    arIcon?: string;
    arPrompt?: string;
    animation?: {
        name: string;
        speed?: number;
    };

    constructor(json: modelType) {
        this.modelName = json.modelName;
        this.annotations = [];
        json.targets.forEach(target => {
            this.annotations.push(new Target(
                Vector3.fromJSON(target.position),
                target.normal ? Vector3.fromJSON(target.normal) : null,
                target.label,
                target.type,
                target.content
            ))
        });
        this.poster = json.poster;
        this.arIcon = json.arIcon;
        this.arPrompt = json.arPrompt;
        this.environment = json.environment;
        this.skybox = json.skybox == "true";
        this.animation = json.animation;
    }

    toHTML(): string {
        const html = `<model-viewer 
            bounds="tight" enable-pan shadow-intensity="1"
            ${this.addModel()}
            ${this.addAR()}
            ${this.addCameraControls()}
            ${this.addPoster()}
            ${this.addEnvironment()}
            
        >
        ${this.addAnnotations()}
        ${this.addProgressBar()}
        ${this.addARButton("View in your space")}
        ${this.addARPrompt()}
        ${this.addNoARButton("AR is not tracking")}
        </model-viewer>`
        return html;
    }

    private addModel() {
        return `src="${this.modelName}"`;
    }

    private addEnvironment() {
        let html = "";
        if (this.environment)
            html += ` environment-image="${this.environment}";`
        if (this.environment != "neutral" && this.skybox)
            html += ` skybox-image="${this.environment}"`;
        return html;
    }

    private addAR() {
        return `ar ar-modes="webxr scene-viewer quick-look"`;
    }

    private addCameraControls() {
        return "camera-controls";
    }

    private addPoster() {
        if (this.poster && this.poster.length > 0)
            return `poster="${this.poster}"`
        return "";
    }

    private addARButton(text: string) {
        const src = this.arIcon ? this.arIcon : `${globalThis.defaultRessourcesPath}/ar_icon.png`;
        return `<button slot="ar-button" id="ar-button" style="background-image: url(${src});">
        ${text}
        </button>`
    }

    private addNoARButton(text) {
        return `<button id="ar-failure">
        ${text}
      </button>`;
    }

    private addAnnotations() {
        let html = "";
        this.annotations.forEach(target => {
            html += target.toHTML();
        });
        return html;
    }

    private addARPrompt() {
        const src = this.arPrompt ? this.arPrompt : `${globalThis.defaultRessourcesPath}/ar_hand_prompt.png"`;
        return `<div id="ar-prompt"><img src="${src}"></div>`
    }

    private addProgressBar() {
        return `<div class="progress-bar hide" slot="progress-bar"><div class="update-bar"></div></div>`
    }

}