import Sidepanel from "./Sidepanel";
import Vector3 from "./Vector3";

export default class Target {
    static counter = 0;
    label?: string;
    type?: string;
    position: Vector3;
    normal?: Vector3;
    content?: Sidepanel;
    slot: number;

    constructor(position: Vector3, normal?: Vector3, label?: string, type?: string, content?: string) {
        this.position = position;
        this.normal = normal;
        this.label = label;
        this.type = type;
        this.slot = Target.counter++;
        this.content = new Sidepanel(content, this.slot);
    }

    toHTML(): string {
        let html: string = `<button class="Hotspot"
        slot="hotspot-${this.slot}"
        data-position="${this.position.toHTML()}" `
        if (this.normal)
            html += `data-normal="${this.normal.toHTML()}"`
        html += `data-visibility-attribute="visible">\n`
        if (this.label)
            html += `<div class="HotspotAnnotation">${this.label}</div>\n`
        html += "</button>";

        return html;
    }


    static addHotspotsEvents() {
        // const app = document.getElementById(`app`);
        // app.addEventListener('click', (target) => Sidepanel.hideAllSidepanels())

        const hotspots = Array.from(document.getElementsByClassName('Hotspot'));

        hotspots.forEach(hotspot => {
            hotspot.addEventListener('click', function handleClick(event) {
                const id = event.target.slot;
                Sidepanel.showSidepanel(id);
            });
        });


    }
}